import React, { useState, forwardRef, useEffect, useRef } from 'react'
import styled from 'styled-components'
import { isMobile } from 'react-device-detect'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import useOnclickOutside from "react-cool-onclickoutside";
import { useSelector } from 'react-redux'
import { RootState } from 'store'

import color from '../color-css/color'
import TextInput from '../TextInput/index'
import ValidationIndicator from '../ValidationIndicator'
import RadioButtonGroup from '../RadioButtonGroup'

import {
  EMPLOYMENT_STATUS, EMPLOYMENT_STATUS_CASUAL
} from '../FormHighOrder/types/AboutYouField'
import ReactRef from '../FormHighOrder/types/ReactRef'


import cursor from '../../assets/images/hand-point-up-1.png'
export type Props = {
  contentDefault?: string
  label?: string
  data?: any
  name?: string
  onSelectedItemProp?: (wasSelected: boolean) => void
  onSelectedValueProp?: (selectedValue: string) => void,
  isValidated?: boolean,
  inputFromProps?: string,
  inputOnAverageFromProps?: string,
  onAverageData?: (data: any) => void
}

const StyledIcon = styled(FontAwesomeIcon)`
    position: absolute;
    color: ${color.blue_darker};
    margin-top: -2.5rem;
    margin-left: 270px;
    height: 0.875rem;
`

interface DropdownContainer {
  visibility: boolean
}

const DropdownContainer = styled.div<DropdownContainer>`
    display: ${props => props.visibility === true ? 'block' : 'none'};
    position: absolute;
    margin-top: 1.25rem;
    width: 18.9375rem;
    height: 14.0625rem;
    background: ${color.white} 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 3.125rem #012F571A;
    border-radius: 0.625rem;
    z-index: 1111111;
    &:before {
        content: "";
        position: absolute;
        left: 9.0625rem;
        top: -1.25rem;
        width: 0;
        height: 0;
        border-top: 0.9375rem solid transparent;
        border-right: 0.9375rem solid #fff;
        border-bottom: 0.875rem solid transparent;
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
`

const DropdownSuggestWrapped = styled.div`
    overflow: auto;
    height: 8.75rem;
    padding: 1.5625rem 1.5625rem 0 2.5rem;
    margin-right: 1rem;
    margin-top: 0.625rem;
    ::-webkit-scrollbar {
        width: 0.8125rem;
      }
    ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #EFEFEF; 
    border-radius: 0.625rem;
    }
    ::-webkit-scrollbar-thumb {
    background: #EFEFEF; 
    border-radius: 0.625rem;
    }
    @media (max-width: 768px) {
      width: 100%;
    }
`

interface DropdownSuggestContentWrapped {
  value?: (input: string) => string,
}

const DropdownSuggestContentWrapped = styled.p<DropdownSuggestContentWrapped>`
    font-family: museo-sans, sans-serif;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.5625rem;
    color: ${color.grey_darker};
    cursor: url(${cursor}), auto;	
    @media (max-width: 768px) {
      width: 100%;
    }
`

const Label = styled.label`
  display: block;
  max-width: 302px;
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  letter-spacing: -0.25px;
  color: ${color.blue_15_percent};
  @media (max-width: 768px) {
    max-width: 100%;
  }
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`

interface Select {
  onInputSucessAsBackgroundColor: boolean,
}

const Select = styled.select<Select>`
  width: calc(100% - 37px);
  height: 1.1875rem;
  background-color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue : color.grey};
  border: none;
  outline-color : ${color.blue};
  color: ${props => props.onInputSucessAsBackgroundColor === true ? color.blue_17_percent : color.grey_dark};
  font-size: 1rem;
  padding: 0.75rem;
  padding-left: 25px;
  margin: 1.25rem 0 0.625rem 0;
  border-radius: 0.625rem;
`

const DropdownSuggestContent = styled.span``

const DropdownSuggestContentKey = styled.div`
    margin-bottom: 0.625rem;
    &:hover ${DropdownSuggestContent} {
        color: ${color.blue_17_percent}
    }
`

const PositioningRadioButtonGroup = styled(RadioButtonGroup)
  `
    margin-top: 2.5rem;
`

type Ref = HTMLInputElement

export const SelectDropdown = forwardRef<Ref, Props>((props: any, aRef) => {

  const getPrefetchData = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[1].data[EMPLOYMENT_STATUS])

    const checkFetchData = (nameParam: string) => {
      return getPrefetchData !== undefined && getPrefetchData[nameParam]?.value !== ""
    }

  const { contentDefault, label, data, name,
    onSelectedItemProp,
    onSelectedValueProp,
    inputFromProps,
    inputOnAverageFromProps,
    isValidated,
    onAverageData } = props;

  const [inputSuccess, setInputSuccess] = useState(false);
  const [validatorVisibilityState, setValidatorVisibility] = useState(false);
  const [showingDropdownState, setShowingDropdown] = useState(false);
  const [textInputContentState, setTextInputContent] = useState(contentDefault);
  const [showOnAverage, setShowOnAverage] = useState(false)
  const [validatorMsg, setValidatorMsg] = useState("")
  const [clickedOutsideState, setClickedOutside] = useState(false);
  const [paymentMethodSelection, setPaymentMethodSelection] = useState("")

  const Refs: ReactRef = {
    [EMPLOYMENT_STATUS_CASUAL]: useRef<HTMLInputElement>(null),
  }

  useEffect(() => {
    onAverageData ? onAverageData({
      [EMPLOYMENT_STATUS_CASUAL]: {
        isValidated: true,
        value: paymentMethodSelection,
      }
    }) : {}
  }, [paymentMethodSelection])

  useEffect(() => {
    inputFromProps ? setTextInputContent(inputFromProps) : {}
    
    if (inputFromProps && isValidated) {
      handleContent(inputFromProps)
    }
  }, [inputFromProps, isValidated])

  const handleOnShow = (isShow: boolean) => {
    setShowingDropdown(isShow)
  }

  const clickOut = useOnclickOutside(() => {
    setShowingDropdown(false)
  })

  const handleContentMobile = (event: any) => {
    inputFromProps !== undefined && setTextInputContent(inputFromProps)
    inputFromProps === undefined && setTextInputContent(event.target.value)
    if (event.target.value === 'Casual' || event.target.value === 'Part time') {
      setShowOnAverage(true)
      setInputSuccess(true)
      setValidatorMsg("")
    } else {
      setShowOnAverage(false)
      setInputSuccess(true)
      setValidatorVisibility(true)
      setValidatorMsg("Thanks")
    }
    onSelectedValueProp ? onSelectedValueProp(textInputContentState) : {}
    onSelectedItemProp ? onSelectedItemProp(true) : {}
  }

  const handleContent = (textContent: string) => {
    setTextInputContent(textContent)

    if (textContent === 'Casual' || textContent === 'Part time') {
      setInputSuccess(true)
      setValidatorVisibility(true)
      setValidatorMsg("")
      setShowOnAverage(true)
    }
    else {
      setInputSuccess(true)
      setValidatorVisibility(true)
      setValidatorMsg("Thanks")
      setShowOnAverage(false)
    }
    onSelectedValueProp ? onSelectedValueProp(textContent) : {}
    onSelectedItemProp ? onSelectedItemProp(true) : {}
  }

  const handleClickOutsideRef = useOnclickOutside(() => {
    setShowingDropdown(false)
    setTextInputContent(textInputContentState)
    setClickedOutside(!clickedOutsideState)
  });

  if (isMobile) {
    return (
      <div>
        {label && <Label>{label} <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>*</LabelIcon></Label>}
        <div>
          <Select
            name={name}
            onChange={(event) => {
              handleContentMobile(event)
            }}
            ref={() => aRef}
            onInputSucessAsBackgroundColor={inputSuccess}
          >
            <option>{contentDefault}</option>
            {data.options.map((item: any, i: any) => {
              return (
                <option value={item}>
                  {item}
                </option>
              )
            })}
          </Select>
        </div>
        {
          validatorMsg
          && <ValidationIndicator isCorrect={inputSuccess}
            {...{ message: validatorMsg, visibility: validatorVisibilityState }}
          />
        }
        <RadioButtonGroup
          visibility={showOnAverage}
          label="On average, how many hours do you work a week?"
          title1="0-9 hours"
          title2="10-19 hours"
          title3="20+ hours"
          onSelectedValue={selectedValue => {
            setPaymentMethodSelection(selectedValue)
          }}
        />
      </div>
    )
  }

  return (
    <div ref={clickOut}>
      {label && <Label>{label} <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>*</LabelIcon></Label>}
      <TextInput placeholder="Select gender" widthSize="15.6875rem" name={name} type="text"
        onShow={isShow => handleOnShow(isShow)}
        inputFromProps={textInputContentState}
        readOnly={true}
        ref={aRef}
        isValidated={validatorVisibilityState}
      />

      <StyledIcon icon={faChevronDown} />

      <DropdownContainer
        visibility={showingDropdownState}
        ref={handleClickOutsideRef}
      >
        <DropdownSuggestWrapped>
          <DropdownSuggestContentWrapped>
            {data.options.map((item: any, i: any) => {
              return (
                <DropdownSuggestContentKey key={i} onClick={(event: any) => {
                  event.preventDefault()
                  handleContent(event.target.textContent)
                }}>
                  <DropdownSuggestContent >{item}</DropdownSuggestContent>
                </DropdownSuggestContentKey>
              )
            })}
          </DropdownSuggestContentWrapped>
        </DropdownSuggestWrapped>
      </DropdownContainer>
      {
        validatorMsg !== ""
        && <ValidationIndicator isCorrect={inputSuccess} {...{ message: validatorMsg, visibility: validatorVisibilityState }} />
      }
      <PositioningRadioButtonGroup
        visibility={showOnAverage}
        label="On average, how many hours do you work a week?"
        title1="0-9 hours"
        title2="10-19 hours"
        title3="20+ hours"
        onSelectedValue={(value) => {
          setInputSuccess(true)
          setValidatorVisibility(true)
          onSelectedValueProp? onSelectedValueProp(value) : {}
          setPaymentMethodSelection(value)
        }}
        inputFromProps={
          inputOnAverageFromProps
        }
      />
    </div >
  )
})

export default SelectDropdown;