import React, { useState, forwardRef, useEffect } from "react";

import AutoSuggest, { RenderSuggestionsContainerParams } from "react-autosuggest";
import styled from 'styled-components'
var match = require('autosuggest-highlight/match')
var parse = require('autosuggest-highlight/parse');
import ValidationIndicator from '../ValidationIndicator'
import { getSuggestions, suggestionRule } from './suggestSource'
import { DropdownFooter, DropdownFooterContent, DropdownFooterImg } from './Footer'
import cursor from '../../assets/images/hand-point-up-1.png'
import color from '../color-css/color'

export type Props = {
  widthSize?: string
  onSelectedItemProp?: (isSuccess: boolean) => void,
  onVisibility?: (isVisible: boolean) => void,
  inputFromProps?: string,
  isValidatedFromProp?: boolean
}

interface EmpAutoSuggestComponent {
  widthSize?: string,
  onHoverInputSuccess?: boolean,
  onInputSucessListener?: (isSucess: boolean) => boolean,
  onVisibility?: (isVisible: boolean) => boolean
}
const StyledAutosuggestWrapper = styled.div<EmpAutoSuggestComponent>`
  border: 1px solid ${color.white};
  outline: none;
  font-family: museo-sans, sans-serif;
  margin-top: 1.25rem;
  margin-bottom: 0.625rem;
& .react-autosuggest__container {
  position: relative;
 }

& .react-autosuggest__input {
  font-family: museo-sans, sans-serif;

  @media screen and (max-width: 768px) {
    padding: 0.8125rem 1.625rem 0.75rem 1.5625rem;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  width: ${props => props.widthSize ? props.widthSize : "34rem"};
  height: 1.1875rem;
  background-color: ${props => props.onHoverInputSuccess === true ? color.blue : color.grey};
  transition: background 0.5s ease-in-out;
  border: 1px solid ${color.white};
  outline : none;
  color: ${props => props.onHoverInputSuccess === true ? color.blue_dark : color.grey_darker};
  font-size: 1rem;
  padding-left: 1.5625rem;
  border-radius: 0.625rem;
  padding: 0.8125rem 5.3125rem 0.75rem 1.5rem;
  cursor: url(${cursor}), auto;	
  & ::placeholder,
  & ::-webkit-input-placeholder {
   color: ${color.grey_darker};
   font-family: museo-sans, sans-serif;
   font-size: 1rem;
  }
  &:focus {
    ::-webkit-input-placeholder { color:transparent; }
    ::-moz-placeholder { color:transparent; }
    border: 1px solid ${color.blue_darker};
  }
 }

 & .react-autosuggest__input:hover{
  transition: border 0.5s ease-in-out;
  border: 1px solid ${color.blue_darker};
 }

& .react-autosuggest__suggestions-container--open {
  display: block;
  position: relative;
  margin-top: 1.125rem;
  width: ${props => props.widthSize ? props.widthSize : "40.9375rem"};
  color: ${color.grey_29_percent};
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: -0.05px;
  @media screen and (max-width: 768px) {
    width: 100%;
  }
  height: 225px;
  background: #fff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 3.125rem ${color.grey_shadow};
  border-radius: 0.625rem;
  & ::before {
    content: "";
    position: absolute;
    left: 20rem;
    @media screen and (max-width: 768px) {
      left: 45vw;
    }
    top: -20px;
    width: 0;
    height: 0;
    border-top: 0.9375rem solid transparent;
    border-right: 0.9375rem solid #fff;
    border-bottom: 14px solid transparent;
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  }
}

& .react-autosuggest__suggestions-list {
  margin: 0;
  padding: 1.5rem;
  list-style-type: none;
  overflow: auto;
  font-weight: 300;
  font-size: 1rem;
  line-height: .625rem;
  height: 7.25rem;
  margin-right: 1rem;
  margin-top: 0.625rem;
  ::-webkit-scrollbar {
      width: 0.8125rem;
    }
  ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #EFEFEF; 
  border-radius: 0.625rem;
  }
  ::-webkit-scrollbar-thumb {
  background: #EFEFEF; 
  border-radius: 0.625rem;
  }

}

& .react-autosuggest__suggestion {
  font-family: museo-sans, sans-serif;
  cursor: url(${cursor}), auto;	
  padding: 0.625rem 1.25rem;
  margin-bottom: 0.75rem;
}

& .react-autosuggest__suggestion:hover {
  font-family: museo-sans, sans-serif;
  color: ${color.blue_17_percent}
}

& .react-autosuggest__suggestion-match {
  font-family: museo-sans, sans-serif;
  font-weight: bold;
}
`

const Label = styled.label`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  color: ${color.blue_17_percent};
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`
const StyledItalic = styled.i`
  font-size: 1rem;
  color: ${color.grey_darker};
  font-weight: 500;
  letter-spacing: -0.05px;
`
type Ref = HTMLInputElement

const EmployerAutosuggestField = forwardRef<Ref, Props>((props: any, aRef) => {
  const { inputFromProps, isValidatedFromProp } = props

  const [inputValue, setValue] = useState("");
  const [suggestions, setSuggestions] = useState<string[]>([]);
  const [inputSuccess, setInputSuccess] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [visibility, setVisibility] = useState(false);
  const [validationMsg, setValidationMsg] = useState("");

  useEffect(() => {
    inputFromProps ? setValue(inputFromProps) : {}
    isValidatedFromProp ? setInputSuccess(isValidatedFromProp) : {}
    isValidatedFromProp ? setVisibility(isValidatedFromProp) : {}

    inputSuccess ?
      setValidationMsg("Nice! We represent over 220,000 employees") : setValidationMsg("Error")
  
    }, [inputFromProps, isValidatedFromProp, inputSuccess])

  const noResultString = 'No results found ...'

  const inputProps = {
    placeholder: "Type your employer here",
    name: "employer",
    value: inputValue,
    // @ts-ignore
    onChange: (event, { newValue, method }) => {
      setValue(newValue);
      setInputSuccess(newValue !== undefined)
    },
    ref: aRef
  }

  const renderSuggestionsContainer = (params: RenderSuggestionsContainerParams) => {
    return (
      <div {...params.containerProps}>
        {params.children}
        {
          isMenuOpen &&
          <DropdownFooter>
            <DropdownFooterContent>Can’t find your employer? Continue typing</DropdownFooterContent>
          </DropdownFooter>
        }
      </div>
    );
  };

  const renderFoundResults = (suggestion: string): React.ReactNode => {

    const matches = match(suggestion, inputValue);
    const parts = parse(suggestion, matches);

    return (
      <span>
        {parts.map((part: { text: string, highlight: boolean }, index: number) => {
          const className = part?.highlight ? 'react-autosuggest__suggestion-match' : '';

          return (
            <span className={className} key={index}>
              {part.text}
            </span>
          );
        })}
      </span>
    );
  }

  return (
    <>
        <Label>Who do you work for? <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>*</LabelIcon></Label>
        <StyledAutosuggestWrapper
          onHoverInputSuccess={inputSuccess}
          onInputSucessListener={props.onInputSucessProp ? props.onInputSucessProp(inputSuccess) : {}}
          onVisibility={props.onVisibility ? props.onVisibility(visibility) : {}}
        >
          <AutoSuggest
            suggestions={suggestions}
            onSuggestionsClearRequested={() => {
              setSuggestions([])
              setIsMenuOpen(false)
            }}
            onSuggestionsFetchRequested={({ value }) => {
              const valueGetter = getSuggestions(value)
              valueGetter.length > 0 ? setSuggestions(valueGetter) : setSuggestions([noResultString])
              setValue(value)
              setIsMenuOpen(true)
            }}
            onSuggestionSelected={(_, { suggestionValue }) => {
              // const isValid = suggestionValue !== undefined && suggestionValue.length > 0
              // setValue(suggestionValue)
              // setInputSuccess(isValid)
              // setVisibility(true)
              const isValid = suggestionValue !== undefined
                && suggestionValue.length > 0
                && suggestionValue !== noResultString
                setInputSuccess(isValid)
              props.onSelectedItemProp ? props.onSelectedItemProp(isValid) : {}

            }}
            shouldRenderSuggestions={(value) => {
              return suggestionRule(value)
            }}
            getSuggestionValue={suggestion => suggestion}
            renderSuggestion={suggestion => {
              if (suggestion !== noResultString) {
                return renderFoundResults(suggestion)
              }
              else {
                return (
                  <span> <StyledItalic>{noResultString}</StyledItalic></span>
                )
              }
            }}
            inputProps={inputProps}
            renderSuggestionsContainer={renderSuggestionsContainer}
            //@ts-ignore
            renderInputComponent={(inputProps) => <input {...inputProps} />}
          />
        </StyledAutosuggestWrapper>
      <ValidationIndicator isCorrect={inputSuccess} {...{ message: validationMsg, visibility }} />
    </>
  );
});

export default EmployerAutosuggestField