import data from './suburb.json'

export interface Suburb {
  name: string
  postcode: number
}
export interface SuburbSuggestion {
  suburbs: Array<Suburb>
}

const suggestionDto: SuburbSuggestion = JSON.parse(JSON.stringify(data))

export const getSuggestions = (value: string): string[] => {
  const inputValue = value.trim().toLowerCase();
  const inputLength = inputValue.length;
  const { suburbs } = suggestionDto
  const suburbNames = suburbs.flatMap(obj => obj.name)
  return inputLength === 0 ? [] : suburbNames.filter(each =>
    each.toLowerCase().slice(0, inputLength) === inputValue
  );
}

export const suggestionRule = (value: string) => value.trim().length > 2;
