import { renderPhoneNumberStrat1, renderPhoneNumberStrat2 } from './phoneNumberRendererStrats'
import { renderDate, renderDateMobile } from './DateRenderer'


const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

export const validatePhoneNumber = (input: string) => {
  let testNumber = input.toString();
  if (testNumber === " ") {
    testNumber.split("")
  }
  return !isNaN(Number(input.toString())) && input.length >= 8
}

export const validateEmail = (input: string) => emailRegex.test(input)

export const validateDate = (input: string) => {
  let check = false;
  const unformatDate = input.split('/');
  let day = parseInt(unformatDate[0], 10);
  let month = parseInt(unformatDate[1], 10);
  let year = parseInt(unformatDate[2], 10);
  if (input.length > 10) {
    return check;
  } else if (input.length === 10) {
    if (year < 1000 || year > 3000 || month == 0 || month > 12)
      return check;

    let monthLength = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

    if (year % 400 == 0 || (year % 100 != 0 && year % 4 == 0))
      monthLength[1] = 29;

    if (day > 0 && day <= monthLength[month - 1]) check = true
  }
  return check
}

export const validatePostcode = (input: string) => !isNaN(Number(input.toString())) && input.length === 4

export const validateExpiryDate = (input: string) => {
  let check = false
  let month = input.substring(0, 2)
  let testMonth = +month
  if (testMonth > 12) return check
  else check = true
  return check
}

export const validateCreditCard = (input: string) => {
  return input.length === 16
}

export const validateBSB = (input: string) => {
  return input.length === 7
}

export const validateAccountNumber = (input: string) => {
  return input.length === 8
}

export const handlePhoneNumber = (inputGetter: string, event: any) => {
  if (inputGetter.startsWith('02') || inputGetter.startsWith('03') ||
    inputGetter.startsWith('07') || inputGetter.startsWith('08')) {
    let result = renderPhoneNumberStrat1(inputGetter)
    if (inputGetter.length === 3 && inputGetter[2] !== ' ') {
      event.target.value = result
    }
    else if (inputGetter.length === 8 && inputGetter[7] !== ' ') {
      event.target.value = result
    }
    else if (inputGetter.length === 10) {
      event.target.value = result
    }
  }
  else if (inputGetter.startsWith('04')) {
    let result = renderPhoneNumberStrat2(inputGetter)
    if (inputGetter.length === 5 && inputGetter[4] !== ' ') {
      event.target.value = result
    }
    else if (inputGetter.length === 9 && inputGetter[8] !== ' ') {
      event.target.value = result
    }
  }

  else event.target.value = inputGetter

}

export const handleDateOnDesktop = (inputGetter: string, event: any) => {
  let result = renderDate(inputGetter)
  if (inputGetter.length === 3 && inputGetter[2] !== ' ') {
    event.target.value = result
  }
  if (inputGetter.length === 8 && inputGetter[7] !== ' ') {
    event.target.value = result
  }
}
