import React, {useState, forwardRef} from "react";
import styled from 'styled-components'

import TextInput from '../TextInput'
import ValidationIndicator from '../ValidationIndicator'
import color from '../color-css/color'

export type Props = {
    isShow?: boolean
    onValidateWorkPostcode?: (success: boolean) => void
    inputFromProps?: string
    isValidatedFromProp?: boolean
}

interface PostCode {
    isShow?: boolean
}
const WorkPostcodeContainer = styled.div<PostCode>`
  animation-name: flickity;
  animation-duration: 1.5s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
      0% {
          opacity:0;
      }
      100% {
          opacity:1;
      }
  };
  display: ${props => props.isShow === true ? 'block' : 'none'};
  margin-top: 2.5rem;
`

const Label = styled.label`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 1.125rem;
  text-align: left;
  color: ${color.blue_15_percent};
`

interface LabelIcon {
  onInputSucessAsLabelIconColor: boolean,
}

const LabelIcon = styled.span<LabelIcon>`
  color: ${props => props.onInputSucessAsLabelIconColor === true ? color.blue_darker : color.red};
`

type Ref = HTMLInputElement

export const WorkPostcode = forwardRef<Ref, Props>((props, aRef) => {
    const {isShow, onValidateWorkPostcode, inputFromProps, isValidatedFromProp} = props
    const [inputSuccess, setIsSuccess] = useState(false);
    const [visibility, setIsVisible] = useState(false);
    const [validatorMsg, setValidatorMsg] = useState('')
    const [value, setValue] = useState('')

    React.useEffect(() => {
      inputFromProps ? setValue(inputFromProps) : {}
      isValidatedFromProp ? setIsSuccess(isValidatedFromProp) : {}
      if ( inputSuccess === true ) {
        setValidatorMsg("Thanks")
      } 
      else {
        setValidatorMsg("Error")
      }
    }),[inputSuccess]

    const handleOnInputSucess = (isSuccess: boolean) => {
        setIsSuccess(isSuccess)
        onValidateWorkPostcode ? onValidateWorkPostcode(isSuccess) : {}
      }
    
    const handleOnVisibility = (isVisible: boolean) => {
      setIsVisible(isVisible)
    }

    return (
        <WorkPostcodeContainer isShow={isShow}>
            <Label>Workplace postcode <LabelIcon onInputSucessAsLabelIconColor={inputSuccess}>*</LabelIcon></Label>
            <TextInput
                placeholder="Enter work postcode"
                widthSize="37.625rem"
                name="postcode"
                maxLength={4}
                onInputSucessCallback={(success:boolean) => {handleOnInputSucess(success)}}
                onVisibility={(visible: boolean) => handleOnVisibility(visible)}
                ref={aRef}
                inputFromProps={value}
                isValidated={inputSuccess}
            />
            <ValidationIndicator isCorrect={inputSuccess} {...{ message: validatorMsg, visibility }}/>
        </WorkPostcodeContainer>
    )
})

export default WorkPostcode
